import CardComponent from './Card'
import Data from './data'
import './Cards.css'
const CardsComponent = function () {
    const data = Data()
    const row = function() {
        const div = data.length / column
        const ent = div + 1;
        if (div > div.toFixed()) {
            console.log("div:",div + 1)
            return div + 1
        }
        else{
            console.log("div:",div)
            return div
        } 
    }
    const column = 3;
    
    console.log(data)
    
    const array2 = () => {
        let arrayGrid = []
        const rowgrid = row();
        let n = 0;
        for (let i = 0; i < rowgrid.toFixed(); i++) {
            const row = [];
            for (let j = n; j < data.length; j++) {
                if (j + 1 > column * (i + 1)) {
                    arrayGrid.push(row)
                    continue;
                }else{
                    n++
                    row.push(data[j])
                }
                if ((j + 1 == data.length)) {
                    arrayGrid.push(row)
                }
                
            }
        }
        return arrayGrid;
    }
    const arrayGrid = array2();
    console.log(arrayGrid)
    return(
        <div className="componente2 container">
            {arrayGrid.map((row)=>{
               return(<div className="row">
                    {row.map(({lang, url, fcolor, scolor})=>{
                        return (
                            <div className="column">
                                <CardComponent lang={lang} url={url} scolor={scolor} fcolor={fcolor}></CardComponent>
                            </div>
                        )
                    })}
                </div>)
            })}
        </div>
    )
}
export default CardsComponent