import { Card } from 'react-bootstrap';
import { Button } from 'react-bootstrap';
import PropTypes from 'prop-types'
const CardComponent = function ({lang, url, scolor, fcolor}) {
    return(
        <div className="componente3">
            <Card style={{ width: '10rem', background:`linear-gradient(to left,${fcolor} , ${scolor} )`}}>
        <Card.Img variant="top" src={url} height="148" />
        <Card.Body>
            <Card.Title>{lang}</Card.Title>
            <Card.Text>
            Texto
            </Card.Text>
            <Button variant="primary">{lang}</Button>
        </Card.Body>
    </Card>
        </div>
    
    )
}
const {string} = PropTypes;
CardComponent.propTypes = {
    lang: string,
    url: string,
    scolor: string,
    fcolor: string
}
export default CardComponent

